
import audioManager from "../main/media/audio-manager";
import mediaManager from "../main/media/media-manager"
import whiteBoardManger from "../main/media/white-board-manager"


export function initMedia(hstRtcEngine) {
  /**
   * 
   * @param {*} mediaType 
   * @param {*} mediaId 
   * @param {*} params
   * @returns 
   */
  hstRtcEngine.prototype.startPublishMedia = function(mediaType, mediaId, params) {
    mediaManager.startPublishMedia(mediaType, mediaId, params)
  };

  /**
   * 
   * @param {*} mediaType 
   * @param {*} mediaId
   * @returns  
   */
  hstRtcEngine.prototype.stopPublishMedia = function(mediaType, mediaId) {
    mediaManager.stopPublishMedia(mediaType, mediaId);
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaType 
   * @param {*} mediaId 
   * @returns Promise
   */
  hstRtcEngine.prototype.startReceiveMedia = function(userId, mediaType, mediaId) {      
    return mediaManager.startReceiveMedia(userId, mediaType, mediaId);
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaType 
   * @param {*} mediaId 
   * @returns Promise
   */
  hstRtcEngine.prototype.stopReceiveMedia = function(userId, mediaType, mediaId) {      
    return mediaManager.stopReceiveMedia(userId, mediaType, mediaId);
  };

  /**
   * 
   * @param {*} userId      ID 
   * @param {*} mediaType   
   * @param {*} mediaId     ID
   * @param {*} renderEle   HTMLcanvasvideo
   * @param {*} params       
   */
  hstRtcEngine.prototype.setMediaRender = function(userId, mediaType, mediaId, renderEle, params) {
    mediaManager.setMediaRender(userId, mediaType, mediaId, renderEle, params);
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaType FIXMEMediaType
   * @param {*} htmlEle 
   */
  hstRtcEngine.prototype.unsetMediaRender = function(userId, mediaType, mediaId) {
    mediaManager.unsetMediaRender(userId, mediaType, mediaId);
  };

  /**
   * 
   * @param {*} value [-12, 12]0
   */
  hstRtcEngine.prototype.setSendMagicAudioValue = function(value) {
    audioManager.setSendMagicAudioValue(value)
  };

  /**
   * 
   * @param {*} mode 012
   */
  hstRtcEngine.prototype.setRecvMagicAudioMode = function(mode) {
    audioManager.setRecvMagicAudioMode(mode)
  };

  /**
   * 
   */
  hstRtcEngine.prototype.createWhiteBoard = function (params) {
    return whiteBoardManger.createWhiteBoard(params);
  }

  /**
   * 
   * @param {*} mediaId 
   */
  hstRtcEngine.prototype.closeWhiteBoard = function (mediaId) {
    return whiteBoardManger.closeWhiteBoard(mediaId);
  }

  /**
   * 
   * @param {*} mediaId null
   * @param {*} mode 123
   * @param {*} param mode3
   */
  hstRtcEngine.prototype.setWhiteBoardDisplayMode =  function(mediaId, mode, param) {
    return whiteBoardManger.setDisplayMode(mediaId, mode, param);
  }
}
