import { MediaOper, MediaType, RecvState, EventType, PubEventType } from "../../common/enums"
import protocolSender from "../util/protocol-sender"
import { promiseHandler } from "../../common/promise-handler"
import { getEventPostfix, tracelog, getBitRate } from "../../common/common"
import mediaManager from "./media-manager"
import eventRegistry from "../../common/event-registry"

class ShareManager {
  constructor() {
    // Screen share media stream
    this.screenShareStream = null;
    this.screenShareStreamRefCount = 0;

    // Make sure one screen share stream
    this.isCreatingStream = false;

    // User defined screen share parameters(init value is default)
    this.screenShareParam = { frameRate: 15, width: 1920, height: 1080, bitRate: 0 };
  }

  /**
   * 
   * @param {*} shareParam { width: 640, height: 360, frameRate: 15, bitRate: 1000 }
   */
  @tracelog
  startScreenShare(shareParam) {
    if (shareParam) { 
      if (!('width' in shareParam) || typeof shareParam.width !== 'number') {
        console.error("No width or invalid width in shareParam!");
        return;
      }

      if (!('height' in shareParam) || typeof shareParam.height !== 'number') {
        console.error("No height or invalid height in shareParam!");
        return;
      }
      
      if (!('frameRate' in shareParam) || typeof shareParam.frameRate !== 'number') {
        console.error("No frameRate or invaid frameRate in shareParam!");
        return;
      }
      
      if (!('bitRate' in shareParam) || typeof shareParam.bitRate !== 'number') {
        console.error("No bitRate or invalid bitRate in shareParam!");
        return;
      }

      // 
      this.setScreenShareParam(shareParam);

      console.log("Set screen share param: ", shareParam);
    }

    protocolSender.sendPublishMediaReq(
      MediaOper.START_PUBLISH, 
      MediaType.SCREEN_SHARE, 
      "0");
  };

  /**
   * 
   */
  stopScreenShare() {
    protocolSender.sendPublishMediaReq(
      MediaOper.STOP_PUBLISH, 
      MediaType.SCREEN_SHARE, 
      "0");
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaId 
   */
  startReceiveScreenShare(userId, mediaId) {
    protocolSender.sendRecvMediaReq(
      RecvState.START_RECV, 
      userId, 
      MediaType.SCREEN_SHARE, 
      mediaId);

    // 
    return promiseHandler(EventType.EVENT_START_RCV_MEDIA + getEventPostfix(userId, MediaType.SCREEN_SHARE, mediaId));
  };

  /**
   * 
   * @param {*} srcUserId 
   * @param {*} mediaId 
   */
  stopReceiveScreenShare(srcUserId, mediaId) {
    protocolSender.sendRecvMediaReq(
      RecvState.STOP_RECV, 
      srcUserId, MediaType.SCREEN_SHARE, 
      mediaId);

    return promiseHandler(EventType.EVENT_STOP_RCV_MEDIA + getEventPostfix(userId, MediaType.SCREEN_SHARE, mediaId));
  };
}

export default new ShareManager();