import rtcEngine from '../main/rtc-engine'
import { promiseHandler } from '../common/promise-handler'
import { EventType } from "../common/enums"

export function initPlatform(HstEngine) {
  /**
   * 
   * @params {*} params 
   */
  HstEngine.prototype.init = function(params) {
    rtcEngine.doInit(params);
    return promiseHandler(EventType.EVENT_INIT);
  };

  /**
   * 
   * @params {*} params 
   */
  HstEngine.prototype.login = function(params) {
    rtcEngine.doLogin(params);
    return promiseHandler(EventType.EVENT_LOGIN);
  };

  /**
   * 
   * @params {*} groupId 
   */
  HstEngine.prototype.joinGroup = function(groupId) {
    rtcEngine.doJoin(groupId);
    return promiseHandler(EventType.EVENT_JOIN_GROUP);
  };

  /**
   * 
   */
  HstEngine.prototype.leaveGroup = function() {
    rtcEngine.doLeave();
    return promiseHandler(EventType.EVENT_LEAVE_GROUP);
  };

  /**
   * 
   */
  HstEngine.prototype.logout = function() {
    rtcEngine.doLogout();
    return promiseHandler(EventType.EVENT_LOGOUT);
  };

  /**
   * 
   */
  HstEngine.prototype.destroy = function() {
    rtcEngine.doDestroy();
  }
}