import { promiseHandler } from "../common/promise-handler"
import onlineManager from "../main/online/online-manager"
import { EventType } from "../common/enums"

export function initOnline(hstRtcEngine) {
  /**
   * 
   */
  hstRtcEngine.prototype.getOnlineUsers = function() {
    onlineManager.getOnlineUsers();
    return promiseHandler(EventType.EVENT_GET_ONLINE_USERS);
  };

  /**
   * 
   * @param {*} params 
   */
  hstRtcEngine.prototype.invite = function(params) {
    onlineManager.invite(params);
    return promiseHandler(EventType.EVENT_INVITE);
  };

  /**
   * 
   * @param {*} params 
   */
  hstRtcEngine.prototype.replyInvite = function(params) {
    onlineManager.replyInvite(params);
  };

  /**
   * 
   * @param {*} params 
   */
  hstRtcEngine.prototype.cancelInvite = function(params) {
    onlineManager.cancelInvite(params);
    return promiseHandler(EventType.EVENT_CANCEL_INVITE);
  };
}
