
class TxRoom {
    constructor() {
        this.serverDomain = 'https://fspwxlite.hst.com:443/weapp/webrtc_room';
        //this.serverDomain = 'https://fspwxlite.haoshitong.com:8443/weapp/webrtc_room';
        this.requestNum = 0;
        this.heart = 0; // 
        this.heartBeatReq = 0;
        this.requestSeq = 0; // id
        this.requestTask = []; // task
    }
  
    /**
     * [request request]
     * @param {options}
     *   url: url
     *   data: 
     *   success: 
     *   fail: 
     *   complete: 
     */
    request(options) {
      var self = this;
      self.requestNum++;
      var req = wx.request({
        url: self.serverDomain + options.url,
        data: options.data || {},
        method: 'POST',
        header: {
          'content-type': 'application/json' // 
        },
        // dataType: 'json',
        success: function (res) {
          if (res.data.code) {
            console.error('' + ', url=' + options.url + ', params = ' + (options.data ? JSON.stringify(options.data) : '') + ', =' + JSON.stringify(res));
            options.fail && options.fail({
              errCode: res.data.code,
              errMsg: res.data.message
            })
          } else {
            options.success && options.success(res);
          }
        },
        fail: function (res) {
          console.error('' + ', url=' + options.url + ', =' + JSON.stringify(res));
          options.fail && options.fail(res);
        },
        complete: function () {
          self.requestNum--;
          console.log('complete requestNum: ', self.requestNum);
        }
      });
      self.requestTask[self.requestSeq++] = req;
      return req;
    }
  
    /**
     * [clearRequest ]
     * @param {options}
     */
    clearRequest() {
      var self = this;
      for (var i = 0; i < self.requestSeq; i++) {
        self.requestTask[i].abort();
      }
      self.requestTask = [];
      self.requestSeq = 0;
    }
  
    getLoginInfo(userID, success, fail) {
      var self = this;
      var data = {};
      if (userID) {
        data.userID = userID;
      }
      self.request({
        url: '/get_login_info',
        data: data,
        success: success,
        fail: fail
      })
    }
  
    getRoomList(index, count, success, fail) {
      var self = this;
      self.request({
        url: '/get_room_list',
        data: {
          index: index,
          count: count,
          roomType: 'trtc'
        },
        success: success,
        fail: fail
      })
    }
  
    createRoom(userID, roomInfo, success, fail) {
      console.log('roomInfo', roomInfo);
      var self = this;
      self.request({
        url: '/create_room',
        data: {
          userID: userID,
          roomInfo: roomInfo,
          roomType: 'trtc'
        },
  
        success: function (res) {
          success && success(res);
        },
  
        fail: fail
      });
    }
  
    enterRoom(userID, roomID, success, fail) {
      var self = this;
      self.request({
        url: '/enter_room',
        data: {
          userID: userID,
          roomID: roomID
        },
        success: function (res) {
          success && success(res);
        },
        fail: fail
      })
    }
  
    quitRoom(userID, roomID, success, fail) {
      var self = this;
      self.request({
        url: '/quit_room',
        data: {
          userID: userID,
          roomID: roomID
        },
        success: success,
        fail: fail
      });
      // self.stopHeartBeat();
    }
  
    startHeartBeat(userID, roomID, success, fail) {
      var self = this;
      self.heart = '1';
      self.heartBeat(userID, roomID, success, fail);
    }
  
    stopHeartBeat() {
      var self = this;
      self.heart = '';
      if (self.heartBeatReq) {
        console.log('');
        self.heartBeatReq.abort();
        self.heartBeatReq = null;
      }
    }
  
    heartBeat(userID, roomID, success, fail) {
      var self = this;
      if (!self.heart) {
        self.clearRequest();
        return;
      }
      self.heartBeatReq = self.request({
        url: '/heartbeat',
        data: {
          userID: userID,
          roomID: roomID
        },
        success: function (res) {
          if (self.heart) {
            console.log('');
            success && success(res);
            setTimeout(() => {
              self.heartBeat(userID, roomID, success, fail);
            }, 7000);
          }
        },
        fail: function (res) {
          fail && fail(res);
          if (self.heart) {
            setTimeout(() => {
              self.heartBeat(userID, roomID, success, fail);
            }, 7000);
          }
        }
      })
    }
  }
  
  export default new TxRoom();
  