import eventRegistry from "./event-registry";

// Promise
export const promiseHandler = function(eventName, fn) {
  return new Promise((resolve, reject) => {
    //FIXMEonce
    eventRegistry.on(eventName, function() {
      // 
      let result = Array.prototype.shift.call(arguments);
      if (parseInt(result) === 0) {
        fn && fn.call(this);
        resolve(...arguments);
      } else {
        reject(...arguments);
      }
    });
  });
};
