
import HstWebSocket from '../util/websocket'
import protocolSender from "../util/protocol-sender"
import { MediaType, DisplayMode, Graphics } from "../../common/enums"
import MsgDispatcher from '../util/msg-dispatcher'
import { asyncWrapper, tracelog } from '../../common/common'
import logger from '../../common/logger'
import eventRegistry from '../../common/event-registry'
import draw from "../util/draw"
import { preloadImage } from "../../common/common"
import PageStore from "./page-store"
import boardManager from "./white-board-manager"

/**
 * 
 */
class ReadOnlyWhiteboard {
  constructor(params) {
    this.isClosing = false;

    // 
    this.pageStore = null;

    // 
    this.firstFullData = true;

    // 
    this.scaleRatio = 1;

    // 
    this.width = 0;
    this.height = 0;

    // CanvasCSS
    this.parentWidth = 0;
    this.parentHeight = 0;

    // Canvas
    this.canvasEle = null;
    this.canvasCtx = null;
    this.canvasSizeChangeCallback = null;

    // CanvasCSS
    this.canvasLeft = 0;
    this.canvasTop = 0;
    this.canvasWidth = 0;
    this.canvasHeight = 0;

    // 
    this.sourcePaints = [];

    // 
    this.msg_seq_id = null;

    // 
    this.displayMode = DisplayMode.DBWZ;
    this.displayParam = null;

    // 
    this.userId = params.userId;
    this.mediaId = params.mediaId;
    this.streamId = params.streamId;
    this.streamToken = params.streamToken;
    this.streamServer = params.streamServer

    // 
    this.msgDispatcher = new MsgDispatcher();
    this.msgDispatcher.addMsgHandler(31000, this._dealAmendData.bind(this)) // 
    this.msgDispatcher.addMsgHandler(31002, this._dealAmendData.bind(this)) // 
    this.msgDispatcher.addMsgHandler(31006, this._dealAmendData.bind(this)) // 
    this.msgDispatcher.addMsgHandler(31110, this._pageChangeHandle.bind(this)) // 
    this.msgDispatcher.addMsgHandler(31112, this._moveGraph.bind(this)) // 
    this.msgDispatcher.addMsgHandler(30000, this._heartBeat.bind(this))
    this.msgDispatcher.addMsgHandler(30004, this._dealFullData.bind(this))
    this.msgDispatcher.addMsgHandler(19005, this._processLoginResp.bind(this));

    // WebSocket
    this.websocket = new HstWebSocket({
      addrUrl: [this.streamServer],
      handler: this.msgDispatcher,
      opened: this._onWebsocketOpen.bind(this),
      closed: this._onWebsocketClosed.bind(this),
      error: this._onWebsocketError.bind(this)
    });

    return this
  }

  /**
   * 
   * @param {*} msg 
   */
  @tracelog
  _processLoginResp(msg) {
    if (msg.result != 0) {
      logger.warn("Login white board receive channel failed!");
    }
  }

  /**
   * WebSocket
   */
  @tracelog
  _onWebsocketOpen() {
    protocolSender.sendLoginRecvChannelReq(this.websocket, {
      mediaType: MediaType.WHITE_BOARD,
      mediaId: "",
      streamId: this.streamId,
      streamToken: this.streamToken
    });
  }

  /**
   * WebSocekt
   */
  @tracelog
  _onWebsocketClosed() {
    if (!this.isClosing) {
      asyncWrapper(()=>{eventRegistry.trigger("restoreWhiteBoard", {
        userId: this.userId,
        mediaId: this.mediaId,
        parentEle: this.parentEle,
        displayMode: this.displayMode,
        displayParam: this.displayParam})
      })
      this.close();
    }
  }

  /**
   * WebSocket
   */
  @tracelog
  _onWebsocketError() {
  }

  /**
   * 
   * @param {*} mode 
   * @param {*} param 
   */
  setDisplayMode(mode, param) {
    if (this.displayMode != mode // 
      || (mode == DisplayMode.DBSF && param && this.displayParam != param)) { // 
      this.displayMode = mode;
      this.displayParam = param;
      this._showWhiteBoard();
    }
  }

  /**
   * 
   */
  @tracelog
  _showWhiteBoardDbwz() {
    // 
    let showRatio = this.parentHeight / this.parentWidth.toFixed(6);
    let dataRatio =  this.height / this.width.toFixed(6);

    // 
    if (showRatio > dataRatio ) {
      // Canvas
      this.canvasWidth = this.parentWidth;
      this.canvasHeight = this.height * this.parentWidth / this.width.toFixed(6);

      // Canvas
      this.canvasLeft = 0;
      this.canvasTop = (this.parentHeight - this.canvasHeight) / 2
    } else { // 
      // Canvas
      this.canvasWidth = this.width * this.parentHeight / this.height.toFixed(6);
      this.canvasHeight = this.parentHeight;

      // Canvas
      this.canvasLeft = (this.parentWidth - this.canvasWidth) / 2;
      this.canvasTop = 0;
    }

    // Canvas
    this.canvasSizeChangeCallback(
      this.canvasLeft,
      this.canvasTop,
      this.canvasWidth, 
      this.canvasHeight)

    // 
    this.scaleRatio = this.canvasHeight / this.height.toFixed(6);
    this.canvasEle.width = this.canvasWidth
    this.canvasEle.height = this.canvasHeight

    this._refreshRender();
  }

  /**
   * 
   */
  _showWhiteBoardDbsy() {
    // 
    let showRatio = this.parentHeight / this.parentWidth.toFixed(6);
    let dataRatio =  this.height / this.width.toFixed(6);

    // 
    if (showRatio > dataRatio ) {
      // Canvas
      this.canvasWidth = this.width * this.parentHeight / this.height.toFixed(6);
      this.canvasHeight = this.parentHeight;

      // Canvas
      this.canvasLeft = 0;
      this.canvasTop = 0;
    } else { // 
      // Canvas
      this.canvasWidth = this.parentWidth;
      this.canvasHeight = this.height * this.parentWidth / this.width.toFixed(6);

      // Canvas
      this.canvasLeft = 0;
      this.canvasTop = 0;
    }

    // Canvas
    this.canvasSizeChangeCallback(
      this.canvasLeft,
      this.canvasTop,
      this.canvasWidth, 
      this.canvasHeight)

    // 
    this.scaleRatio = this.canvasHeight / this.height.toFixed(6);

    // Canvas
    this.canvasEle.width = this.canvasWidth
    this.canvasEle.height = this.canvasHeight

    this._refreshRender();
  }

  /**
   * 
   */
  _showWhiteBoardDbsf() {
    // Canvas
    this.canvasWidth = this.width * this.displayParam.toFixed(6) / 100;
    this.canvasHeight = this.height * this.displayParam.toFixed(6) / 100;

    // Canvas
    if (this.canvasWidth >= this.parentWidth) {
      this.canvasLeft = 0;
      // this.parentEle.style.overflowX = 'scroll';
    } else {
      this.canvasLeft = (this.parentWidth - this.canvasWidth) / 2;
      // this.parentEle.style.overflowX = 'hidden';
    }
    
    if (this.canvasHeight >= this.parentHeight) {
      this.canvasTop = 0;
      // this.parentEle.style.overflowY = 'scroll';
    } else {
      this.canvasTop = (this.parentHeight - this.canvasHeight) / 2;
      // this.parentEle.style.overflowY = 'hidden';
    }

    // Canvas
    this.canvasSizeChangeCallback(
      this.canvasLeft,
      this.canvasTop,
      this.canvasWidth, 
      this.canvasHeight)

    // 
    this.scaleRatio = this.displayParam.toFixed(6) / 100;

    // Canvas
    this.canvasEle.width = this.canvasWidth
    this.canvasEle.height = this.canvasHeight

    this._refreshRender();
  }

  /**
   * 
   */
  _showWhiteBoard() {
    if (!this.canvasEle || !this.canvasCtx) {
      console.warn("Invalid canvas render!")
      return;
    }

    // 
    if (this.displayMode == DisplayMode.DBWZ) {
      this._showWhiteBoardDbwz();
    } else if (this.displayMode == DisplayMode.DBSY) {
      this._showWhiteBoardDbsy();
    } else if (this.displayMode == DisplayMode.DBSF) {
      this._showWhiteBoardDbsf();
    } else {
      logger.error("Unknown display mode: " + this.displayMode);
    }
  }

  /**
   * Canvas
   * @param {*} parentEle 
   */
  @tracelog
  setRenderElement(renderEle, params) {
    logger.log("Set render element: " + JSON.stringify(params));
    if (renderEle) {
      this.canvasEle = renderEle;

      // this.canvasEle.width = this.width
      // this.canvasEle.height = this.height

      this.parentWidth = params.parentWidth;
      this.parentHeight = params.parentHeight;

      this.canvasSizeChangeCallback = params.canvasSizeChange;

      // Canvas
      this.canvasCtx = this.canvasEle.getContext('2d');

      // 
      this._showWhiteBoard();
    } else {
      // 
      this._emptyCanvas();

      // Canvas
      if (this.canvasEle) {
        this.canvasEle.width = 0
        this.canvasEle.height = 0
        this.canvasCtx = null;
        this.canvasEle = null;
      }
    
      this.canvasSizeChangeCallback = null;

      this.parentWidth = 0;
      this.parentHeight = 0;
    }
  }

  /**
   * canvas
   * @param {*} boardInfo 
   */
  _initCanvas (boardInfo) {
    // 
    this.width = boardInfo.width
    this.height = boardInfo.height

    // URL
    boardManager._getFileUrl(boardInfo.convert_file_path, boardInfo.page)
    
    // Render
    this._refreshRender()
  }

  /**
   * 
   */
  _emptyCanvas() {
    if (this.canvasCtx) {
      this.canvasCtx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
    }
  }

  /**
   * URL
   */
  _waitGetImageUrl() {
    const convertPath = this.pageStore.getConvertFilePath()
    return new Promise(function(resolve, reject) {
      let count = 0
      let handle = setInterval(() => {
        ++count
        if (boardManager.cacheFileUrlMap.get(convertPath)) {
          clearInterval(handle)
          resolve()
        } else {
          if (count > 10) { // 5
            clearInterval(handle)
            reject("URL")
          }
        }
      }, 500)
    })
  }

  /**
   * 
   */
  _drawImage() {
    let self = this
    const convertPath = this.pageStore.getConvertFilePath()
    const curPage = this.pageStore.getCurPage()
    const fullPath = `${convertPath}/${curPage}.jpg`

    return new Promise(function(resolve, reject) {
      let imgUrl = boardManager.cacheFileUrlMap.get(convertPath).get(fullPath)
      wx.getImageInfo({
        src: imgUrl,
        success: function(res) {
          if (self.canvasEle) {
            let seal = self.canvasEle.createImage();
            seal.onload = function() {
              logger.log("")
              if (self.canvasCtx) {
                self.canvasCtx.drawImage(
                  seal, 
                  0, 
                  0, 
                  self.canvasWidth, 
                  self.canvasHeight);
              }
              resolve()
            }
            seal.onerror = function(res) {
              console.error("", res)
              reject()
            }
            seal.src = res.path;
          } 
        },
        fail: function(res) {
          logger.warn("Get image info failed!")
          reject()
        }
      })
    })
  }

  /**
   * render
   */
  _refreshRender() {
    let self = this
    self._emptyCanvas()
    if (self.pageStore.getFilePath()) {
      self._waitGetImageUrl().then(()=>{
        logger.log("Image url is ready.")
        self._drawImage().then(()=>{
          logger.log("Draw image finished.")
          self._drawAllPaint()
        }).catch(()=>{
          logger.log("Draw image failed!")
        })
      }).catch((err)=>{
        logger.warn(err)
      })
    } else {
      logger.log("")
      self._drawAllPaint()
    }
  }

  /**
   * 
   * @param {*} type 
   * @param {*} obj 
   */
  _drawPaint (type, obj) {
    if (this.canvasCtx) {
      try {
        draw[type](this.canvasCtx, obj, this.scaleRatio)
      } catch (err) {
        logger.error("Draw type: " + type + ", error: " + err);
      }
    }
  }

  /**
   * 
   */
  _drawAllPaint() {
    if (this.pageStore) {
      let paintObjs = this.pageStore.findPagePaints(this.pageStore.getCurPage())
      paintObjs.map((item) => {
        this._drawPaint(item.type, item[item.type])
      })
    }
  }

  /**
   * 
   * @param {*} pageData 
   * @param {*} paintObj 
   */
  _drawAmendData (pageData, paintObj) {
    if (paintObj.type !== Graphics.CURVE) {
      this._emptyCanvas()
      this._drawAllPaint()
    } else { // 
      const paint = this.pageStore.findPaintByGid(pageData, paintObj[paintObj.type].gid)
      if (paint) {
        this._drawPaint(paint.type, paint[paint.type])
      }
    }
  }

  /**
   * // 
   * @param {*} paintObj 
   */
  _dealAmendData (paintObj) {
    this._checkMsgSeq(paintObj);

    let pageData = this.pageStore.findPagePaints(this.pageStore.getCurPage())
    if (paintObj[paintObj.type].action === 1) { // 
      this.pageStore.addPaintObj(pageData, paintObj)
      this._drawAmendData(pageData, paintObj)
    } else if (paintObj[paintObj.type].action === 2) { // 
      this.pageStore.amendPaintObj(pageData, paintObj)
      this._drawAmendData(pageData, paintObj)
    } else if (paintObj[paintObj.type].action === 3) { // 
      this.pageStore.deletePaintObj(pageData, paintObj[paintObj.type].gid)
      this._drawAllPaint()
    }
  }

  /**
   * 
   * @param {*} pageObj 
   */
  _pageChangeHandle (pageObj) {
    this._checkMsgSeq(pageObj);

    let pageItem = pageObj[pageObj.type]
    if (pageItem.action === 0) { // 
      this.pageStore.setCurPage(pageItem.pid)
      this._refreshRender()
      // 
      eventRegistry.trigger("onWhiteBoardCurPageChanged", {
        mediaId: this.mediaId, 
        curPage: this.pageStore.getCurPage()
      })
    } else if (pageItem.action === 1) { // 
      this.pageStore.addPage(pageItem)
    } else if (pageItem.action === 2) { // 
      this.pageStore.deletePage(pageItem)
    } else if (pageItem.action === 3) { // 
      this._emptyCanvas()
      this.pageStore.clearPaints()
    } else { // 
      logger.warn("Unexpected action: " + pageItem.action);
    }
  }

  /**
   * 
   * @param {*} paintObj 
   */
  _moveGraph (paintObj) {
    this._checkMsgSeq(paintObj);

    let pageData = this.pageStore.findPagePaints(paintObj.gmov.pid)
    paintObj.gmov.graphs.map((gid) => {
      this.pageStore.movePaintObj(pageData, gid, paintObj.gmov)
    })
    this._drawAllPaint()
  }

  /**
   * 
   */
  @tracelog
  close() {
    this.isClosing = true;

    this.pageStore = null;
    this.msgDispatcher = null;

    boardManager.removeWhiteBoard(this.mediaId);

    if (this.intervalHandle) {
      clearInterval(this.intervalHandle);
      this.intervalHandle = null;
    }

    if (this.parentEle && this.canvasEle) {
      this.parentEle.removeChild(this.canvasEle);
      //this.parentEle = null;
      this.canvasEle = null;
    }

    if (this.websocket) {
      this.websocket.close();
      this.websocket = null;
    }
  }
  
  /**
   * 
   * @param {*} msg 
   */
  _dealFullData(msg) {
    // 
    this.sourcePaints = this.sourcePaints.concat(msg.data)
    // 
    if (msg.sync_end) {
      logger.log("Get white board full data complete.");
      this.pageStore = new PageStore(this.sourcePaints)

      if (this.sourcePaints[0].board) {
        this._initCanvas(this.sourcePaints[0].board)
      } else {
        logger.error("Invalid source paints: " + JSON.stringify(this.sourcePaints));
        this.sourcePaints = [];
        return;
      }

      // 
      if (this.firstFullData) {
        if (!boardManager.tryRestoreWhiteBoardRender(this.mediaId)) {
          eventRegistry.trigger("onRemoteMediaAdd", { 
            userId: null, 
            mediaType: MediaType.WHITE_BOARD, 
            mediaId: this.mediaId,
            mediaName: this.pageStore.getBoardName(),
            totalPage: this.pageStore.getPageCount(),
            curPage: this.pageStore.getCurPage()
          });
        }
        this.firstFullData = false;
      }

      this.sourcePaints = [];
    }
  }

  /**
   * 
   * @param {*} msg 
   */
  _checkMsgSeq(msg) {
    if (!this.msg_seq_id // 
      || (this.msg_seq_id !== null && msg.seq_id - this.msg_seq_id !== 1)) { // 
      protocolSender.sendGetWhiteBoardFullDataReq(this.websocket);
    }
    this.msg_seq_id = msg.seq_id;
  }

  /**
   * 
   * @param {*} msg 
   */
  _heartBeat (msg) {
    this._checkMsgSeq(msg);
  }
}

export default ReadOnlyWhiteboard