import { MediaType, MediaOper, RecvState, EventType, RecvMagicMode } from "../../common/enums"
import protocolSender from "../util/protocol-sender"
import { promiseHandler } from "../../common/promise-handler"
import { getEventPostfix } from "../../common/common"
import mediaManager from "./media-manager"
import logger from "../../common/logger"

class AudioManager {
  constructor() {
    // Current microphone device
    this.curMicDevId = null

    // Current speaker device
    this.curSpkDevId = null

    // magic audio, 0: original
    this.audioMagicValue = 0
    this.recvMagicAudio = false
  }

  /**
   * 
   * @param {*} devId 
   */
  startPublishAudio(devId) {
    if (!devId) { // TODO
      logger.error("Invalid devId!")
      return
    }

    // 
    this.setCurMicDevId(devId)

    // SDKMediaIdappdef_mic
    // mediaIdappdef_mic
    let mediaId = null
    if (this.audioMagicValue == 0) {
      mediaId = "appdef_mic"  // 
    } else { // 
      mediaId = "appdef_mic_magic"
    }

    protocolSender.sendPublishMediaReq(
      MediaOper.START_PUBLISH, 
      MediaType.AUDIO, 
      mediaId)
    
    // 
    // return promiseHandler(EventType.EVENT_START_PUB_AUDIO)
  }

  /**
   * 
   * @param {*} devId deviceId
   */
  stopPublishAudio(devId) {
    let mediaId = null
    if (this.audioMagicValue == 0) {
      mediaId = "appdef_mic"  // 
    } else { // 
      mediaId = "appdef_mic_magic"
    }

    protocolSender.sendPublishMediaReq(
      MediaOper.STOP_PUBLISH,
      MediaType.AUDIO, 
      mediaId)

    // 
    // return promiseHandler(EventType.EVENT_STOP_PUB_AUDIO)
  }

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaId 
   */
  startReceiveAudio(userId, mediaId) {
    if (!userId || !mediaId) {
      logger.error("Invalid srcUserId or mediaId!")
      return
    }

    protocolSender.sendRecvMediaReq(
      RecvState.START_RECV, 
      userId, 
      MediaType.AUDIO, 
      mediaId)

    // 
    return promiseHandler(EventType.EVENT_START_RCV_MEDIA + getEventPostfix(userId, MediaType.AUDIO, mediaId))
  }

  /**
   * 
   * @param {*} srcUserId 
   * @param {*} mediaId 
   */
  stopReceiveAudio(srcUserId, mediaId) {
    if (!srcUserId || !mediaId) {
      logger.error("Invalid srcUserId or mediaId!")
      return
    }

    protocolSender.sendRecvMediaReq(
      RecvState.STOP_RECV, 
      srcUserId, 
      MediaType.AUDIO, 
      mediaId)

    return promiseHandler(EventType.EVENT_STOP_RCV_MEDIA + getEventPostfix(userId, MediaType.AUDIO, mediaId))
  }

  /**
   * 
   * @param {*} devId 
   */
  chooseSpkDevice(devId) {
    this.curSpkDevId = devId

    // downPeerConnection
    for (const downPc of mediaManager.downPcMap.values()) {
      if (downPc.videoEle) {
        this.setAudioSpeaker(downPc.videoEle)
      }  
    }
  }

  /**
   * 
   * @param {*} value [-12, 12]0
   */
  setSendMagicAudioValue(value) {
    if (this.audioMagicValue == value) return

    this.audioMagicValue = value
    logger.log("Set send audio magic to " + value)

    // 
    for (const pc of mediaManager.upPcMap) {
      for (const track of pc.upTracks) {
        if (track.mediaType == MediaType.AUDIO) {
          protocolSender.sendSetAudioMagic(pc.websocket, track.streamId, value)
          break // PC
        }
      }
    }
  }

  /**
   * 
   * @param {*} mode 012
   */
  setRecvMagicAudioMode(mode) {
    if (mode != RecvMagicMode.FOLLOW 
      && mode != RecvMagicMode.ONLY_ORIGIN 
      && mode != RecvMagicMode.ONLY_MAGIC) {
      logger.error("Invalid recv audio magic mode: " + mode)
      return
    }
    logger.log("Set recv audio magic mode: " + mode)
    this.recvMagicAudio = mode
  }

  /**
   * video
   * @param {*} videoEle 
   */
  setAudioSpeaker(videoEle) {
    if (typeof videoEle.sinkId !== 'undefined') {
      videoEle.setSinkId(this.curSpkDevId).then(() => { 
        logger.log("Set speaker " + this.curSpkDevId + " success.") 
      }).catch((err) => { 
        logger.error("Set speaker " + this.curSpkDevId + " failed!", err) 
      })
    } else {
      logger.error("Invalid media element!")
    }
  }

  /**
   * 
   * TODO: curMicDevnull
   * 
   * @param {*} devId 
   */
  setCurMicDevId(devId) {
    if (devId) {
      this.curMicDevId = devId
    } else {
      logger.error("Invalid microphone device!")
    }
  }

  /**
   * 
   */
  getCurMicDevId() {
    return this.curMicDevId
  }

  /**
   * 
   */
  getCurSpkDevId() {
    return this.curSpkDevId
  }

  /**
   * constraints
   * @param {*} devId 
   */
  buildConstraints(devId) {
    let audioConstraints = { deviceId: devId }
    return { audio: audioConstraints }
  }
}

export default new AudioManager()