import protocolSender from "../util/protocol-sender"
import Store from "../util/store"
import eventRegistry from "../../common/event-registry"

class MessageManager {
  constructor() {
    Store.msgDispatcher.addMsgHandler(11000, this._onRecvUserMsg.bind(this));
    Store.msgDispatcher.addMsgHandler(11006, this._onRecvGroupMsg.bind(this));
    Store.msgDispatcher.addMsgHandler(11010, this._onRecvServerAck.bind(this));
  }

  /**
   * 
   * @param {*} params 
   */
  sendUserMsg(params) {
    protocolSender.sendUserMsg(params);
  };

  /**
   * 
   * @param {*} params 
   */
  sendGroupMsgWithWhiteList(params) {
    protocolSender.sendGroupMsgWithWhiteList(params);
  };

  /**
   * 
   * @param {*} params 
   */
  sendGroupMsgWithBlackList(params) {
    protocolSender.sendGroupMsgWithBlackList(params);
  };

  /**
   * 
   * @param {*} params 
   */
  sendGroupMsg(params) {
    protocolSender.sendGroupMsg(params);
  };

  /**
   * 
   * @param {*} data 
   */
  _onRecvUserMsg(data) {
    eventRegistry.trigger("onRecvUserMsg", {
      srcUserId: data.src_user_id,
      msg: data.msg,
      msgId: data.msg_id
    });
    protocolSender.sendUserMsgAck(data);
  };

  /**
   * 
   * @param {*} data 
   */
  _onRecvGroupMsg(data) {
    eventRegistry.trigger("onRecvGroupMsg", {
      srcUserId: data.src_user_id,
      groupId: data.group_id,
      msg: data.msg,
      msgId: data.msg_id
    });
    protocolSender.sendGroupMsgAck(data);
  }

  /**
   * Ack
   * @param {*} data 
   */
  _onRecvServerAck(data) {
    // TODO: 
  }
}

export default new MessageManager();