import { initPlatform } from './export/platform-export'
import { initSubscribe } from './export/subevent-export'
import { initMedia } from './export/media-export'
import { initOnline } from './export/online-export'
import { initEnums} from './export/enums-export'
import { initMessage } from "./export/message-export"

function HstRtcEngine() {
  if (process.env.NODE_ENV !== "production" && !(this instanceof HstRtcEngine)) {
    console.error("HstRtcEngine is a constructor and should be called with the `new` keyword");
  }
}

initSubscribe(HstRtcEngine);
initPlatform(HstRtcEngine);
initMedia(HstRtcEngine);
initOnline(HstRtcEngine);
initEnums(HstRtcEngine);
initMessage(HstRtcEngine);

export default HstRtcEngine;