import { getCtrlPoint, stringToColor } from '../../common/common'
const draw = {
  // 
  line: (canv, obj, ratio) => {
    canv.beginPath();
    canv.lineCap = "round"; // 
    canv.lineWidth = obj.brush_size * ratio; // 
    canv.strokeStyle = stringToColor(obj.color); // 

    canv.moveTo(obj.x1 * ratio, obj.y1 * ratio)
    canv.lineTo(obj.x2 * ratio, obj.y2 * ratio)

    canv.stroke();
    canv.closePath();
  },
  // 
  curve: (canv, obj, ratio) => {
    canv.beginPath();
    canv.lineCap = "round"; // 
    canv.lineWidth = obj.brush_size * ratio; // 
    canv.strokeStyle = stringToColor(obj.color); // 
    const points = obj.points.map((item) => {
      return {
        X: item[0] * ratio,
        Y: item[1] * ratio
      }
    })
    if (obj.brush_size > 4) {
      canv.moveTo(points[0].X, points[0].Y);
      for (let i = 1; i < points.length; i++) {
        let ctrlP = getCtrlPoint(points, i - 1)
        canv.bezierCurveTo(ctrlP.pA.x, ctrlP.pA.y, ctrlP.pB.x, ctrlP.pB.y, points[i].X, points[i].Y)
      }
    } else {
      for (let i = 1; i < points.length; i++) {
        canv.lineTo(points[i].X, points[i].Y)
      }
    }
    canv.stroke()
    canv.closePath()
  },
  // 
  text: (canv, obj, ratio) => {
    canv.beginPath();
    canv.font = `${obj.fontsize * 4 / 3 * ratio}px "${obj.color}"`;
    canv.textBaseline = "top";
    canv.fillStyle = stringToColor(obj.color);
    var indexLine = 0;
    for (var txt of obj.text.split('\r\n')) {
      canv.fillText(txt, obj.left * ratio, (indexLine * 22) + obj.top * ratio);
      indexLine++;
    }
    canv.closePath();
  },
  // 
  rectangle: (canv, obj, ratio) => {
    canv.beginPath()
    canv.lineWidth = obj.brush_size * ratio // 
    canv.strokeStyle = obj.color // 

    let x1 = parseInt(obj.x1) * ratio;
    let y1 = parseInt(obj.y1) * ratio;
    let x2 = parseInt(obj.x2) * ratio;
    let y2 = parseInt(obj.y2) * ratio;

    canv.rect(x1, y1, (x2 - x1), (y2 - y1));
    canv.closePath();
    canv.stroke();
  }
}

export default draw