import Store from '../util/store'
import { MediaType, MediaOper, EventResult, RecvState, EventType } from "../../common/enums"
import { asyncWrapper, getAddrs, getEventPostfix, tracelog, checkParam } from "../../common/common"
import logger from "../../common/logger"
import videoManager from "./video-manager"
import audioManager from "./audio-manager"
import shareManager from "./share-manager"
import whiteBoardManger from "./white-board-manager"
import eventRegistry from '../../common/event-registry'

/**
 * 
 */
class MediaManager {
  constructor() {
    // BindId : UpPeerConnection
    this.upPcMap = new Map();

    // BindId : DownPeerConnection
    this.downPcMap = new Map();

    // To be restored down tracks 
    this.restoreDownTracks = new Array();

    // 
    Store.msgDispatcher.addMsgHandler(14106, this.onNotifyUserMediaState.bind(this));
    Store.msgDispatcher.addMsgHandler(14109, this.onNotifyGroupMediaStates.bind(this));
    Store.msgDispatcher.addMsgHandler(14110, this.onNotifyUserJoinGroup.bind(this));
    Store.msgDispatcher.addMsgHandler(14111, this.onNotifyUserLeaveGroup.bind(this));
    Store.msgDispatcher.addMsgHandler(18003, this.onRecvStreamRes.bind(this));

    // 
    eventRegistry.on("restoreDownTrack", this._onRestoreDownTrack.bind(this));
  }

  /**
   * 
   * @param {*} params 
   * @returns track
   */
  @tracelog
  tryRestoreMediaRender(params) {
    if (this.restoreDownTracks.length == 0) {
      logger.log("Empty restore tracks.");
      return false;
    }

    let track = this.restoreDownTracks[0];
    if (params.userId !== track.userId
      || params.mediaType !== track.mediaType
      || params.mediaId !== track.mediaId) {
      logger.log("Different track.");
      return false;
    }

    if (track.videoEle) {
      this.setMediaRender(track.userId, track.mediaType, track.mediaId, track.videoEle);
    }

    this.restoreDownTracks.shift();
    if (this.restoreDownTracks.length > 0) {
      asyncWrapper(()=>{this._doStartReceiveMedia(this.restoreDownTracks[0])})
    }
      
    return true;
}

  /**
   * 
   * @param {*} params 
   */
  _doStartReceiveMedia(params) {
    this.startReceiveMedia(params.userId, params.mediaType, params.mediaId).then(()=>{
      logger.log("Start receive media success.");
    }).catch((e)=>{
      logger.error("Start receive media failed, err: " + e);
      this.restoreDownTracks.shift();
    })
  }

  /**
   * 
   * @param {*} params 
   */
  @tracelog
  _onRestoreDownTrack(params) {
    this.restoreDownTracks.push(params);
    if (this.restoreDownTracks.length == 1) {
      this._doStartReceiveMedia(params);
    }
  }

  /**
   * 
   * @param {*} mediaType 
   * @param {*} mediaId 
   * @param {*} params
   * @returns 
   */
  @tracelog
  startPublishMedia(mediaType, mediaId, params) {
    switch(mediaType) {
      case MediaType.AUDIO:
        audioManager.startPublishAudio(mediaId);
        break;
      case MediaType.VIDEO:
        videoManager.startPublishVideo(mediaId, params);
        break;
      case MediaType.SCREEN_SHARE:
        shareManager.startScreenShare(params);
        break;
      case MediaType.WHITE_BOARD:
        whiteBoardManger.startPublishWhiteBoard(mediaId);
        break;
      default:
        logger.error("Unknown media type: " + mediaType);
        break;
    }
  };

  /**
   * 
   * @param {*} mediaType 
   * @param {*} mediaId
   * @returns  
   */
  @tracelog
  stopPublishMedia(mediaType, mediaId) {
    switch(mediaType) {
      case MediaType.AUDIO:
        audioManager.stopPublishAudio();
        break;
      case MediaType.VIDEO:
        videoManager.stopPublishVideo(mediaId);
        break;
      case MediaType.SCREEN_SHARE:
        shareManager.stopScreenShare();
        break;
      case MediaType.WHITE_BOARD:
        whiteBoardManger.stopPublishWhiteBoard(mediaId);
        break;
      default:
        logger.error("Unknown media type: " + mediaType);
        break;
    }
  };

  /**
   * FIXMEPromise
   * @param {*} userId 
   * @param {*} mediaType 
   * @param {*} mediaId 
   * @returns Promise
   */
  @tracelog
  startReceiveMedia(userId, mediaType, mediaId) {      
    switch(mediaType) {
      case MediaType.AUDIO:
        return audioManager.startReceiveAudio(userId, mediaId);
      case MediaType.VIDEO:
        return videoManager.startReceiveVideo(userId, mediaId);
      case MediaType.SCREEN_SHARE:
        return shareManager.startReceiveScreenShare(userId, mediaId);
      case MediaType.WHITE_BOARD:
        return whiteBoardManger.startReceiveWhiteBoard(userId, mediaId);
      default:
        return new Promise((resolve, reject) => {
          reject("Unknown media type: " + mediaType);
        });
    }
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaType 
   * @param {*} mediaId 
   */
  @tracelog
  stopReceiveMedia(userId, mediaType, mediaId) {      
    switch(mediaType) {
      case MediaType.AUDIO:
        return audioManager.stopReceiveAudio(userId, mediaId);
      case MediaType.VIDEO:
        return videoManager.stopReceiveVideo(userId, mediaId);
      case MediaType.SCREEN_SHARE:
        return shareManager.stopReceiveScreenShare(userId, mediaId);
      case MediaType.WHITE_BOARD:
        return whiteBoardManger.stopReceiveWhiteBoard(userId, mediaId);
      default:
        logger.error("Unknown media type: " + mediaType);
    }
  }

  /**
   * 
   * @param {*} userId      ID 
   * @param {*} mediaType   
   * @param {*} mediaId     ID
   * @param {*} renderEle   HTMLcanvasvideo
   * @param {*} params       
   */
  @tracelog
  setMediaRender(userId, mediaType, mediaId, renderEle, params) {
    switch(mediaType) {
      case MediaType.AUDIO: 
      case MediaType.VIDEO:
      case MediaType.SCREEN_SHARE:
        console.warn("Unexpected mediaType: " + mediaType);
        break;
      case MediaType.WHITE_BOARD:
        whiteBoardManger.setWhiteBoardRender(mediaId, renderEle, params);
        break;
      default:
        logger.error("Unknown media type: " + mediaType);
        break;
    }
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaType FIXMEMediaType
   * @param {*} mediaId 
   */
  @tracelog
  unsetMediaRender(userId, mediaType, mediaId) {
    logger.trace("Calling unsetMediaRender: " + userId + ", " + mediaType);
    switch(mediaType) {
      case MediaType.AUDIO: 
      case MediaType.VIDEO:
      case MediaType.SCREEN_SHARE:
        console.warn("Unexpected mediaType: " + mediaType);
        break;
      case MediaType.WHITE_BOARD:
        whiteBoardManger.unsetWhiteBoardRender(mediaId);
        break;
      default:
        logger.error("Unknown media type: " + mediaType);
        break;
    }
  };

  /**
   *  
   * @param {*} videoEle
   * @param {*} streamId 
   */
  _setMediaRender(userId, mediaType, mediaId, videoEle, videoParam){
    if (userId == Store.userInfo.userId) { // 
      if (mediaType == MediaType.VIDEO) {
        videoManager.setLocalVideoRender(mediaId, videoEle, videoParam);
      } else if (mediaType == MediaType.SCREEN_SHARE) {
        shareManager.setLocalScreenShareRender(videoEle);
      } else {
        logger.error("Unexpected media type: " + mediaType);
      }
    } else { // 
      let pc = this.findDownPcByUserMeida(userId, mediaType, mediaId);
      if (pc) {
        videoEle.srcObject = pc.mediaStream;
        pc.videoEle = videoEle;
        if (this.curSpkDevId) { // 
          audioManager.setAudioSpeaker(videoEle);
        }
      } else {
        logger.error("Cannot find down peer connection!");
      }
    }
  };

  /**
   * 
   * unset
   * @param {*} userId 
   * @param {*} videoEle 
   */
  _unsetMediaRender(userId, videoEle) {
    if (!videoEle || !('srcObject' in videoEle)){
      logger.error("Invalid videoEle!");
      return;
    }

    if (userId == Store.userInfo.userId) { // 
      if (videoEle.srcObject) {
          for (const track of videoEle.srcObject.getTracks()) {
            track.stop();
          }
          videoEle.srcObject = null;
      } else {
        logger.warn("Invalid srcObject!");
      }
    } else { // 
      videoEle.srcObject = null;
    }
  }

  /**
   * 
   * @param {*} msg 
   * @param {*} socket 
   */
  onNotifyUserJoinGroup(msg, socket) {
    eventRegistry.trigger("onUserJoinGroup", msg.user_id);
  }

  /**
   * 
   * @param {*} msg 
   * @param {*} socket 
   */
  onNotifyUserLeaveGroup(msg, socket) {
    eventRegistry.trigger("onUserLeaveGroup", msg.user_id);
  }

    /**
   * 
   * @param {*} operation 
   * @param {*} userId 
   * @param {*} mediaType 
   * @param {*} mediaId 
   */
  _getEventType(operation, userId, mediaType, mediaId) {
    return (
      (operation == RecvState.START_RECV) 
        ? EventType.EVENT_START_RCV_MEDIA 
        : EventType.EVENT_STOP_RCV_MEDIA
      ) + getEventPostfix(userId, mediaType, mediaId)
  }

  /**
   * /
   * @param {*} data 
   * @param {*} socket 
   */
  onRecvStreamRes(data, socket) {
    try {
      if (data.group_id.length === 0 || data.media_id.length === 0) {
        throw "Invalid recv stream response!!!" + data;
      }
  
      // websocket
      let streamServer = null;
      if (data.stream_server) {
        streamServer = getAddrs("ws", data.stream_server);
        if (!streamServer) {
          throw "Cannot find webrtc gateway: " + data.stream_server;
        }
      } else {
        throw "Cannot find 'stream_server' in msg!"
      }
  
      if (data.media_type !== MediaType.WHITE_BOARD) {
        let downPc = this.downPcMap.get(data.bind_id);
        if (data.recv === RecvState.STOP_RECV) {
          if (downPc) {
            downPc.stopRecvStream({
              userId: data.user_id,
              mediaType: data.media_type,
              mediaId: data.media_id
            });
          } else {
            logger.warn("Have not recv the media, do nothing!");
          }
        } else if (data.recv === RecvState.START_RECV) {
          if (!downPc) {
            downPc = new DownPeerConnection(data.bind_id);
            this.downPcMap.set(data.bind_id, downPc);
            logger.log("Add a down peer connection, count = " + this.downPcMap.size);
          }
          downPc.startRecvStream({
            userId: data.user_id,
            mediaType: data.media_type,
            mediaId: data.media_id,
            streamId: data.stream_id,
            streamServer: streamServer, 
            streamToken: data.subscribe_token
          });
        } else {
          throw "Unknown media operation: " + data.recv;
        }
      } else { // 
        if (data.recv === RecvState.START_RECV) {
          whiteBoardManger.onStartRecvWhiteBoardRes({
            userId: data.user_id,
            mediaType: data.media_type,
            mediaId: data.media_id,
            streamId: data.stream_id,
            streamServer: streamServer, 
            streamToken: data.subscribe_token
          });
        } else {
          whiteBoardManger.onStopRecvWhiteBoardRes({
            userId: data.user_id,
            mediaType: data.media_type,
            mediaId: data.media_id,
            streamId: data.stream_id,
          });
        }
      }
  
      eventRegistry.trigger(
        this._getEventType(data.recv, data.user_id, data.media_type, data.media_id),
        EventResult.SUCCESS);
    } catch (err) {
      logger.error(err)
      eventRegistry.trigger(
        this._getEventType(data.recv, data.user_id, data.media_type, data.media_id),
        EventResult.FAILED);
    }
  }

  /**
   * 
   * @param {*} data 
   * @param {*} socket 
   */
  onNotifyUserMediaState(data, socket) {
    if (data.media_type != MediaType.SCREEN_SHARE
      && data.media_type != MediaType.AUDIO
      && data.media_type != MediaType.VIDEO
      && data.media_type != MediaType.WHITE_BOARD){
      logger.warn("Unexpected media type: ", data.media_type);
      return;
    }

    let params = {
      userId: data.user_id, 
      mediaType: data.media_type,
      mediaId: data.media_id,
      mediaName: data.media_description
    };

    // /
    if (data.operation == MediaOper.STOP_PUBLISH){
      eventRegistry.trigger("onUnPublishMedia", params);
    } else if (data.operation == MediaOper.START_PUBLISH) {
      eventRegistry.trigger("onPublishMedia", params);
    }
  }

  /**
   * 
   * @param {*} data 
   * @param {*} socket 
   */
  onNotifyGroupMediaStates(data, socket) {
    // FIXME
    if (Store.restoring) {
      Store.restoring = false
      return
    }
    
    let groupUsers = [];
    for (const user of data.user_info) {
      groupUsers.push(user.user_id);
    }

    // 
    if (groupUsers.length > 0){
      eventRegistry.trigger("onGroupUserList", groupUsers);
    }

    // 
    for (const user of data.user_info) {
      if (user.media_info.length <= 0) { 
        continue; 
      }
    
      for (const media of user.media_info) {
        let params = {
          userId: user.user_id,
          mediaType: media.media_type,
          mediaId: media.media_id,
          mediaName: media.media_description
        };
        eventRegistry.trigger("onPublishMedia", params);
      }
    }
  }
}

export default new MediaManager();