import protocolSender from "../util/protocol-sender"
import Store from "../util/store"
import { RecvState, MediaType, MediaOper, EventType } from "../../common/enums"
import { promiseHandler } from "../../common/promise-handler"
import logger from "../../common/logger"
import { tracelog, getBitRate } from "../../common/common"
import mediaManager from "./media-manager"
import { getEventPostfix } from '../../common/common'

class VideoManager {
  constructor() {
    // User defined video parameters
    // Media Id: { width: xx, height: xx, frameRate: xx, bitRate: xx }
    this.videoParamMap = new Map();

    // Default video parameters
    this.defaultVideoParam = { frameRate: 15, width: 640, height: 480, bitRate: 0 };
  }
  /**
   * 
   * @param {*} deviceId 
   * @param {*} videoParam 
   */
  @tracelog
  startPublishVideo(deviceId, videoParam) {
    if (!deviceId) { // TODO
      logger.error("Invalid deviceId!");
      return;
    }

    // videoParam: { width: 640, height: 360, frameRate: 15, bitRate: 1000 }
    if (videoParam) { 
      if (!('width' in videoParam) || typeof videoParam.width !== 'number') {
        logger.error("No width or invalid width in videoParam!");
        return;
      }

      if (!('height' in videoParam) || typeof videoParam.height !== 'number') {
        logger.error("No height or invalid height in videoParam!");
        return;
      }
      
      if (!('frameRate' in videoParam) || typeof videoParam.frameRate !== 'number') {
        logger.error("No frameRate or invaid frameRate in videoParam!");
        return;
      }
      
      if (!('bitRate' in videoParam) || typeof videoParam.bitRate !== 'number') {
        logger.error("No bitRate or invalid bitRate in videoParam!");
        return;
      }

      // 
      this.setVideoParam(deviceId, videoParam);
    }

    protocolSender.sendPublishMediaReq(MediaOper.START_PUBLISH, MediaType.VIDEO, deviceId);
    
    // 
    // return promiseHandler(EventType.EVENT_START_PUB_VIDEO);
  };

  /**
   * 
   * @param {*} deviceId 
   */
  @tracelog
  stopPublishVideo(deviceId) {
    protocolSender.sendPublishMediaReq(MediaOper.STOP_PUBLISH, MediaType.VIDEO, deviceId);

    // 
    // return promiseHandler(EventType.EVENT_STOP_PUB_VIDEO);
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaId 
   */
  @tracelog
  startReceiveVideo(userId, mediaId) {
    protocolSender.sendRecvMediaReq(
      RecvState.START_RECV, 
      userId, 
      MediaType.VIDEO, 
      mediaId);

    // 
    return promiseHandler(EventType.EVENT_START_RCV_MEDIA + getEventPostfix(userId, MediaType.VIDEO, mediaId));
  };

  /**
   * 
   * @param {*} userId 
   * @param {*} mediaId 
   */
  @tracelog
  stopReceiveVideo(userId, mediaId) {
    protocolSender.sendRecvMediaReq(
      RecvState.STOP_RECV, 
      userId, 
      MediaType.VIDEO, 
      mediaId);

    return promiseHandler(EventType.EVENT_STOP_RCV_MEDIA + getEventPostfix(userId, MediaType.VIDEO, mediaId));
  };
}

export default new VideoManager();
