import msgManger from "../main/message/message-manager"

export function initMessage(hstRtcEngine) {
  /**
   * 
   * @param {*} params 
   */
  hstRtcEngine.prototype.sendUserMsg = function(params) {
    msgManger.sendUserMsg(params);
  };

  /**
   * 
   * @param {*} params 
   */
  hstRtcEngine.prototype.sendGroupMsgWithWhiteList = function(params) {
    msgManger.sendGroupMsgWithWhiteList(params);
  };

  /**
   * 
   * @param {*} params 
   */
  hstRtcEngine.prototype.sendGroupMsgWithBlackList = function(params) {
    msgManger.sendGroupMsgWithBlackList(params);
  };

  /**
   * 
   * @param {*} params 
   */
  hstRtcEngine.prototype.sendGroupMsg = function(params) {
    msgManger.sendGroupMsg(params);
  };
}