// Backbone.Events(Backbone.js 1.1.2)
// 1
// 2

const slice = [].slice

/**
 * 
 * @param {*} eventName 
 * @param {*} callback 
 * @param {*} context 
 */
export function on (eventName, callback, context) {
  const _registry = this._registry || (this._registry = {})
  const eventEntry = _registry[eventName] || (_registry[eventName] = {events: [], callbacks: []})
  eventEntry.callbacks.push({ callback: callback, context: context })
  _triggerCachedEvents(eventEntry, callback, context);
  return this
}

/**
 * 
 * @param {*} eventName 
 * @param {*} callback 
 * @param {*} context 
 */
export function off (eventName, callback, context) {
  // 
  if (!eventName && !callback && !context) {
    delete this._registry
    return this
  }
  // 
  const allEvents = eventName ? [eventName] : Object.keys(this._registry)
  for (let i = 0, l = allEvents.length; i < l; i++) {
    let event = allEvents[i]
    const callbacks = this._registry[event].callbacks;
    if (callbacks) {
      const retain = this._registry[event].callbacks = []
      if (callback || context) {
        for (let j = 0, k = callbacks.length; j < k; j++) {
          const callbackItem = callbacks[j]
          if ((callback && callback !== callbackItem.callback) 
            || (context && context !== callbackItem.context)) {
            retain.push(callbackItem)
          }
        }
      }
      if (!retain.length) {
        delete this._registry[event];
      }
    } else {
      console.warn("Empty callbacks of event: " + event);
    }
  }
  return this
}

/**
 * 
 * @param {*} eventName 
 */
export function trigger (eventName) {
  if (!this._registry) {
    this._registry = {};
  }
  const args = slice.call(arguments, 1)
  const eventEntry = this._registry[eventName] || (this._registry[eventName] = {events:[], callbacks:[]});
  if (eventEntry.callbacks.length > 0) {
    _triggerEvents(eventEntry, args, this)
  } else {
    if (eventEntry.events.length < 1024) {
      eventEntry.events.push(args);
    } else {
      console.warn("Events queue is full, event: " + eventName);
    }
  }
  return this
}

/**
 * 
 * @param {*} eventEntry 
 * @param {*} callback 
 * @param {*} context 
 */
function _triggerCachedEvents(eventEntry, callback, context) {
  if (eventEntry.events.length > 0) {
    for (const event of eventEntry.events) {
      callback.apply(context || this, event);
    }
    eventEntry.events = [];
  }
}

/**
 * 
 * @param {*} eventEntry 
 * @param {*} args 
 * @param {*} context 
 */
function _triggerEvents (eventEntry, args, context) {
  let i = -1;
  const l = eventEntry.callbacks.length;
  while (++i < l) {
    const callbackItem = eventEntry.callbacks[i];
    callbackItem.callback.apply(callbackItem.context || context, args);
  }
}

export default {
  on, off, trigger
}
