import MsgDispatcher from "./msg-dispatcher"
import { detectOS, getAddrs } from "../../common/common"
import logger from "../../common/logger";
/**
 * 
 * 
 * Store
 */
class Store {
  constructor() {
    // 
    this.websocket = null;

    // 
    this.restoring = false;

    // 
    this.userInfo = {
      appId: null,
      groupId: null,
      userId: null,
    };

    // 
    this.loginParams = {
      accessUrl: "",
      token: null,
      companyId: "",
      forceLogin: 0,    // 0 1
      extendInfo: "",
      mutexType: "web",
      registered: 1     // 01
    }

    // Protocol handler
    this.msgDispatcher = new MsgDispatcher();
  }
  
  /**
   * ClientID
   */
  getClientId() {
    return "{" + this.userInfo.appId 
      + ";" + this.userInfo.groupId 
      + "};" + this.userInfo.userId;
  }

  /**
   * CPURL
   */
  buildGetGateWayUrl(accessUrl) {
    return accessUrl + "?appType=24&appId=" + this.appId 
      + "&ver=" + SDK_VERSION 
      + "&comid=" + this.companyId 
      + "&os=wechat"
      + "&token=" + this.token;
  }
}

export default new Store();
