import { EventResult, EventType } from "../../common/enums"
import Store from "../util/store"
import eventRegistry from "../../common/event-registry"

class OnlineManager {
  constructor() {
    // 
    this.cachedUserList = null;

    // 
    Store.msgDispatcher.addMsgHandler(12120, this.onGetOnlineUsersRes.bind(this));
    Store.msgDispatcher.addMsgHandler(12123, this.onOnlineUsersNotify.bind(this));
    Store.msgDispatcher.addMsgHandler(14001, this.onInviteRes.bind(this));
    Store.msgDispatcher.addMsgHandler(14002, this.onNotifyInvite.bind(this));
    Store.msgDispatcher.addMsgHandler(14004, this.onNotifyIgnoreInvite.bind(this));
    Store.msgDispatcher.addMsgHandler(14007, this.onCancelInviteRes.bind(this));
    Store.msgDispatcher.addMsgHandler(14008, this.onNotifyCancelInvite.bind(this));
  }

  /**
   * 
   */
  getOnlineUsers() {
    let msg = {
      business: "PS",
      id: 12119,
      seq_id: (Math.floor(Math.random()*4096))
    };
    Store.websocket.send(JSON.stringify(msg));
  };

  /**
   * 
   * @param {*} data 
   */
  onGetOnlineUsersRes(data) {
    if (data.result != 0) {
      eventRegistry.trigger(
        EventType.EVENT_GET_ONLINE_USERS, 
        EventResult.FAILED);
      return;
    }
    
    let pageInfo = {
      totalPages: data.page_info.total_pages,
      curPage: data.page_info.cur_page
    };
    
    let userInfo = [];
    for (const user of data.member_state_list) {
      let u = {
        userId: user.uid,
        onlineInfo: []
      };
  
      for (const info of user.online_info) {
        let state = {
          mutexType: info.mutex_type,
          customState: info.custom_state,
          state: info.state,
          extendInfo: info.extend_info
        };
        u.onlineInfo.push(state);
      }
  
      userInfo.push(u);
    }
  
    let params = {
      pageInfo: pageInfo,
      userInfo: userInfo
    };
  
    eventRegistry.trigger(
      EventType.EVENT_GET_ONLINE_USERS, 
      EventResult.SUCCESS, 
      params);

    this.cachedUserList = null;
  };

  /**
   * 
   * @param {*} params 
   */
  invite(params) {
    let msg = {
      business: "GS",
      id: 14000,
      seq_id: params.seqId,
      group_id: params.groupId,
      callee_info: [],
      extend_info: params.extendInfo
    };

    // userId
    for (const userId of params.calleeInfo) {
      msg.callee_info.push({user_id: userId});
    }

    Store.websocket.send(JSON.stringify(msg));
  };

  /**
   * 
   * @param {*} params 
   */
  replyInvite(params) {
    let msg = {
      business: "GS",
      id: 14003,
      seq_id: params.seqId,
      group_id: params.groupId,
      operate: params.operation,
      extend_info: params.extendInfo
    };
    Store.websocket.send(JSON.stringify(msg));
  };

  /**
   * 
   * @param {*} params 
   */
  cancelInvite(params) {
    let msg = {
      business: "GS",
      id: 14006,
      seq_id: params.seqId,
      group_id: params.groupId,
      callee_info: params.calleeInfo
    };
    Store.websocket.send(JSON.stringify(msg));
  };

  /**
   * 
   * @param {*} data 
   */
  onOnlineUsersNotify(data) {
    let result = data.result;
    let params = {
      userId: data.user_id,
      mutexType: data.mutex_type,
      state: data.operation,
      customState: data.custom_state,
      extendInfo: data.extend_info
    };
    eventRegistry.trigger("onOnlineUserState", params);
  };

  /**
   * 
   * @param {*} data 
   */
  onInviteRes(data) {
    let params = {
      seqId: data.seq_id,
      userId: data.user_id,
      result: data.result
    };
    eventRegistry.trigger("onInviteReply", params);
  };

  /**
   * 
   * @param {*} data 
   */
  onNotifyInvite(data) {
    let params = {
      seqId: data.seq_id,
      groupId: data.group_id,
      userId: data.user_id,
      userName: data.user_name,
      extendInfo: data.extend_info
    };
    eventRegistry.trigger("onCommingInvite", params);
  };

  /**
   * 
   * @param {*} data 
   */
  onNotifyIgnoreInvite(data) {
    let params = {
      groupId: data.group_id,
      userId: data.user_id,
      userName: data.user_name
    };
    eventRegistry.trigger(EventType.EVENT_NOTICE_IGNORE_INVITE, params);
  };

  /**
   * 
   * @param {*} data 
   */
  onCancelInviteRes(data) {
    let result = data.result;
    let params = {
      seqId: data.seq_id,
      userId: data.user_id,
    };
    eventRegistry.trigger(EventType.EVENT_CANCEL_INVITE, result, params);
  };

  /**
   * 
   * @param {*} data 
   */
  onNotifyCancelInvite(data) {
    let params = {
      seqId: data.seq_id,
      userId: data.user_id,
    };
    eventRegistry.trigger(EventType.EVENT_NOTICE_CANCEL_INVITE, params);
  };
}

export default new OnlineManager();