import logger from "../../common/logger"

/**
 * websocket
 */
class HstWebSocket {
  constructor(options) {
    logger.log(options);
    this.socket = {};
    this.addrUrl = options.addrUrl;
    this.proxyIndex = 0;
    this.isShowLog = true;
    this.handler = options.handler;
    this.opened_hook = options.opened || function() {}; // socket
    this.closed_hook = options.closed || function() {}; // socket
    this.error_hook = options.error || function() {};
    this.limitConnect = this.getTimer();
    this.connectStatus = true
    this._createWebsocket();
    return this;
  }

  /**
   * websocket
   */
  _createWebsocket() {
    if (!(this.addrUrl instanceof Array)) {
      throw new Error("value is not Array");
    }
    if (this.proxyIndex < this.addrUrl.length) {
      let options = {
        url: this.addrUrl[this.proxyIndex],
        success: function(res) {
          logger.info("Create websocket success.");
        },
        fail: function(res) {
          logger.error("Create websocket failed!" + res);
        },
        complete: function(res) {
          logger.info("Create websocket complete.");
        }
      }
      this.socket = wx.connectSocket(options);
      if (!this.socket) {
        logger.error("Connect socket failed: " + this.addrUrl);
        return;
      }

      this._eventBind();
    } else {
      logger.error("Unexpected!");
    }
  }

  /**
   * 
   * @param {*} socket 
   */
  _eventBind() {
    let self = this
    this.socket.onOpen(function(res) {
      logger.log("onOpen: " + JSON.stringify(res));
      self.opened_hook(arguments);
    });

    this.socket.onClose(function(res){
      logger.log("onClose: " + JSON.stringify(res));
      logger.info("websocket ");
      self.closed_hook(arguments)
    });

    this.socket.onMessage(function(res) {
      let msg = JSON.parse(res.data);
      if (msg.id != 30000) { // 
        logger.log("===> Recv msg from " + self.addrUrl + ": " + res.data);
      }
      self.handler.onMessage(msg);
    });

    this.socket.onError(function(res){
      logger.warn(res);
      self.error_hook(arguments);
      if (self.socket !== undefined) {
        self.socket.close();
      }
    })
  }
  
  /**
   * 
   */
  reconnect() {
    if (!this.connectStatus) return
    this.limitConnect(function() {
      this.proxyIndex++;
    });

    this.createWebsocket();
  }

  /**
   * websocket
   */
  getSocket() {
    if (this.socket && this.socket.readyState === 1) {
      return this.socket;
    }
    return null;
  }

  /**
   * 
   */
  getTimer() {
    var timerArray = [];
    return function(fn) {
      timerArray.push(new Date().getTime());
      var timer = new Date().getTime();
      if (timer - timerArray[0] > 5000) {
        timerArray = [];
        fn.apply(this, arguments);
      }
    };
  }

  /**
   * 
   * @param {*} val 
   */
  setAddr(val) {
    if (!(val instanceof Array)) {
      throw new Error("value is not Array");
    }
    this.addrUrl = val;
    this.proxyIndex = 0;
    this.createWebsocket();
  }

  /**
   * 
   * @param {*} msg 
   */
  send(msg) {
    if (this.socket && this.socket.readyState === 1) {
      logger.log("<=== Send msg to " + this.addrUrl + ": " + msg);
      this.socket.send({
        data: msg,
        success: function(res) {
        },
        fail: function(res) {
          logger.error("Send failed!");
        },
        complete: function(res) {
        }
      });
    } else {
      logger.error("Send msg failed: " + msg);
    }
  }

  /**
   * websocket
   */
  close() {
    if (this.socket) {
      this.socket.close();
      this.socket = null;
    }
  }
}

export default HstWebSocket;
